import React, { Component } from 'react';
import { Helpers } from 'react-scroll';

class Blogs extends Component {

    render() {
        return(
            <div className="blogs">
                <div style={{backgroundColor: `#7CA982`, width: `100%`, height: `5px`}}></div>
                <div style={{backgroundColor: `#ABC8AF`, width: `100%`, height: `5px`}}></div>
                <div className="container-fluid blogs-container">
                    <div className="container item-wrapper">

                        <div className="row">
                            <div className="blogs-item">
                                <div className="blogs-header">
                                    <div className="blogs-title"><div>O que  React?</div>
                                    </div>
                                </div>
                                <hr></hr>
                                <div className="hidden-xs col-sm-6 blogs-img">
                                <a target="_blank" href="https://www.devmedia.com.br/react-native-tutorial/32860">
                                        <img
                                            className="img-responsive"
                                            src={require("../images/react.png")}
                                            role={"presentation"} />
                                        </a>
                                </div>
                                <div className="col-xs-12 col-sm-6 blogs-content-wrapper">
                                    <div className="blogs-content">
                                        <div>
                                            &nbsp;
                                            Por se tratar de uma biblioteca nova, com recursos novos, 
                                            muita gente se questiona sobre a real utilidade da mesma, 
                                            e se vale a pena investir em incorporar mais uma tecnologia 
                                            a seus projetos. Por outro lado, muitos desenvolvedores 
                                            consideram a biblioteca eficiente e recomendvel, 
                                            principalmente por duas razes:
                                        </div>
                                        <br></br>
                                       <ul>
                                             Com o React a sua aplicao, bem como toda a lgica interna 
                                            a ela,  toda escrita e executada em JavaScript, o que te possibilita 
                                            ter um UI (User Interface) totalmente nativo. Logo, voc no tem de 
                                            assumir nenhum compromisso tipicamente associado ao UI da HTML5. 
                                        </ul>
                                        <br></br>
                                        <ul>
                                             O React introduz uma abordagem nova e altamente funcional para a 
                                            construo de interfaces de usurio: a interface do usurio  simplesmente
                                             expressa como uma nova funo do estado atual do aplicativo. .
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="blogs-item">
                                <div className="blogs-header">
                                    <div className="blogs-title"><div>O que  JavaScript?</div>
                                    </div>
                                </div>
                                <hr></hr>
                                <div className="hidden-xs col-sm-6 blogs-img">
                                <a target="_blank" href="https://developer.mozilla.org/pt-BR/docs/Aprender/JavaScript">
                                    <img
                                        className="img-responsive"
                                        src={require("../images/js.png")}
                                        role={"presentation"} />
                                    </a>
                                </div>
                                <div className="hidden-sm hidden-md hidden-lg col-xs-12 blogs-img-sm">
                                    <img
                                        className="img-responsive"
                                        src={require("../images/react.png")}
                                        role={"presentation"} />
                                </div>
                                <div className="col-xs-12 col-sm-6 blogs-content-wrapper">
                                    <div className="blogs-content">
                                        <div>
                                            &nbsp;
                                             uma linguagem de programao que pode ser aplicada em um documento HTML 
                                            e usada para criar interaes dinmicas em sites. Ela foi inventada por 
                                            Brendan Eich, co-fundador do projeto Mozilla, da Fundao Mozilla e 
                                            daCorporaoMozilla.
                                        </div>
                                        <br></br>
                                        <div>
                                            &nbsp;
                                            Voc pode fazer muita coisa comJavaScript. Voc vai comear com coisas simples 
                                            como ajustar layouts, fazer coisas acontecerem quando botes so clicados, slides 
                                            e galerias de imagem mas a medida que voc for ganhandoexperincia com a 
                                            linguagem, voc poder criar jogos, animaes grficas em 2D e 3D, aplicativos com 
                                            base de dados e muito mais.
                                        </div> 
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="row">
                            <div className="blogs-item">
                                <div className="blogs-header">
                                    <div className="blogs-title"><div>Cascading Style Sheets </div>
                                    </div>
                                </div>
                                <hr></hr>
                                <div className="hidden-xs col-sm-6 blogs-img">
                                <a target="_blank" href="https://www.hostinger.com.br/tutoriais/o-que-e-css-guia-basico-de-css/#gref">
                                    <img
                                        className="img-responsive"
                                        src={require("../images/css.jpg")}
                                        role={"presentation"} />
                                    </a>
                                </div>
                                <div className="col-xs-12 col-sm-6 blogs-content-wrapper">
                                    <div className="blogs-content">
                                        <div>
                                            &nbsp;
                                            O CSS  uma ferramenta muito potente que possibilita criar diversas funcionalidades 
                                            ao invs de usar JavaScript ou outra linguagem mais pesada. Se usado com moderao, 
                                            CSS pode viabilizar uma tima experincia ao desenvolvedor e usurios das pginas web.
                                        </div>
                                        <br></br>
                                        <div>
                                            &nbsp;
                                            Imagine que o seu site tem o fundo todo preto, o background, e o contedo  escrito 
                                            em branco, para causar o destaque. Ao invs de criar diversas tags em .html para 
                                            determinar que o contedo seja em branco, voc pode aplicar de forma automtica em 
                                            todas as pginas com uso do CSS. 
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        )
    }
}

export default Helpers.Element(Blogs);



